import React from 'react';
import ReactDOM from 'react-dom';
import './WeatherData.scss';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import whiteRainImg from '../assets/white-rain.png';
import rainWhiteSmallImg from '../assets/rain-mini.png';
import cloudySmallImg from '../assets/cloudy-sm.png';
import stormyBlackImg from '../assets/stormy-black.png';
import rainBlackImg from '../assets/rain-black.png';
import cloudyBlackImg from '../assets/cloudy-black.png';
import rainSurgeImg from '../assets/rain-surge.png';

class WeatherData extends React.Component{
  constructor(props){
    super(props);

    var today = new Date();
    var tomorrow = new Date();
    var dayAfter = new Date();
    tomorrow.setDate(today.getDate()+1);
    dayAfter.setDate(tomorrow.getDate()+1);
    this.state = {
        startDate: tomorrow,
        endDate: dayAfter

    };
  }

  componentWillReceiveProps({dates}){
    if(dates.startDate && dates.endDate){
    this.setState({startDate: dates.startDate, endDate:dates.endDate});
    }
  }

  getSectionClassName = (date,isToday) => {
    const strDate = this.state.startDate.getDate();
    const endDate = this.state.endDate.getDate();
    let todayClassName;
    if(((strDate < date) && (date < endDate)) || (date === strDate) || (date === endDate)){
      if(isToday){
        todayClassName = 'today-section highlight';
      }
      else{
        if((date === 27) || (date === 28) || (date === 29) || (date === 30)){
          //evedate
          todayClassName = 'other-day-section highlight';
        }
        else{
          todayClassName = 'other-day-section highlight';
        }
      }
    }
    else{
      if(isToday){
        todayClassName = 'today-section';
      }
      else{
        if((date === 27) || (date === 28) || (date === 29) || (date === 30)){
          todayClassName = 'other-day-section';
        }
        else{
          todayClassName = 'other-day-section';
        }
      }
    }
    return todayClassName;

  }

  render(){
    const {startDate, endDate, days, startDateString, endDateString, endDateYearString, showDatePicker, promoCode} = this.state;
    const selectionRange = {
        startDate: startDate,
        endDate: endDate,
        key: 'selection',
    };
  return(
    <div className="weather-section">
      <div id="22-Jun" className={this.getSectionClassName(22,true)}>
        <p className={"today-text"}>Today<br/>22 Jun</p>
        <p className="weather-text">Cloudy</p>
        <div className="weather-img-big">
          <img src={whiteRainImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">24 31</p>

        <div className="hour-section hour-section1">
            <span className="time-text">6am</span>
            <div className="weather-img">
            <img src={rainWhiteSmallImg} className="rain-sm-img"/>
            </div>
            <div className="temp-text">26</div>
        </div>
        <div className="hour-section hour-section2">
            <span className="time-text">9am</span>
            <div className="weather-img">
              <img src={rainWhiteSmallImg} className="rain-sm-img"/>
            </div>
            <span className="temp-text">27</span>
        </div>
        <div className="hour-section hour-section3">
            <span className="time-text">12pm</span>
            <div className="weather-img">
            <img src={rainWhiteSmallImg} className="rain-sm-img"/>
            </div>
            <span className="temp-text">28</span>
        </div>
        <div className="hour-section hour-section4">
            <span className="time-text">3pm</span>
            <div className="weather-img">
              <img src={cloudySmallImg} className="rain-sm-img"/>
            </div>
            <span className="temp-text">29</span>
        </div>
        <div className="hour-section hour-section5">
            <span className="time-text">6pm</span>
            <div className="weather-img">
              <img src={cloudySmallImg} className="rain-sm-img"/>
            </div>
            <span className="temp-text">26</span>
        </div>
        <div className="hour-section hour-section6">
            <span className="time-text">9pm</span>
            <div className="weather-img">
              <img src={rainWhiteSmallImg} className="rain-sm-img"/>
            </div>
            <span className="temp-text">24</span>
        </div>
      </div>
      <div id="other1" className={this.getSectionClassName(23,false)}>
        <p className="day-text">Sat<br/>
            23 Jun</p>
        <p className="weather-text">Cloudy</p>
        <div className="weather-img">
        <img src={cloudyBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">30  <span>26</span></p>
      </div>
      <div id="other2" className={this.getSectionClassName(27,false)}>
        <p className="day-text">Wed<br/>
            27 Jun</p>
        <p className="weather-text">Stormy</p>
        <div className="weather-img">
        <img src={stormyBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">29  <span>24</span></p>
      </div>
      <div id="other3" className={this.getSectionClassName(24,false)}>
        <p className="day-text">Sun<br/>
            24 Jun</p>
        <p className="weather-text">Rain</p>
        <div className="weather-img">
        <img src={rainBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">29  <span>24</span></p>
      </div>
      <div id="other4" className={this.getSectionClassName(28,false)}>
        <p className="day-text">Thu<br/>
            28 Jun</p>
        <p className="weather-text">Stormy</p>
        <div className="weather-img">
        <img src={stormyBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">31  <span>24</span></p>
      </div>
      <div id="other5" className={this.getSectionClassName(25,false)}>
        <p className="day-text">Mon<br/>
            25 Jun</p>
        <p className="weather-text">Rain</p>
        <div className="weather-img">
        <img src={rainBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">32  <span>24</span></p>
      </div>
      <div id="other6" className={this.getSectionClassName(29,false)}>
        <p className="day-text">Fri<br/>
            29 Jun</p>
        <p className="weather-text">Stormy</p>
        <div className="weather-img">
        <img src={stormyBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">29  <span>24</span></p>
      </div>
      <div id="other7" className={this.getSectionClassName(26,false)}>
        <p className="day-text">Tue<br/>
            26 Jun</p>
        <p className="weather-text">Cloudy</p>
        <div className="weather-img">
        <img src={cloudyBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">25  <span>24</span></p>
      </div>
      <div id="other8" className={this.getSectionClassName(30,false)}>
        <p className="day-text">Sat<br/>
            30 Jun</p>
        <p className="weather-text">Cloudy</p>
        <div className="weather-img">
        <img src={cloudyBlackImg} className="big-weather-img"/>
        </div>
        <p className="degree-text">27  <span>24</span></p>
      </div>
    </div>
  );
  }
}

const mapStateToProps = (state) => ({
    dates:state.dates
});

export default connect(mapStateToProps, null)(WeatherData);



